// src/components/Navbar.js
import React, {useState,useEffect} from 'react';

function Navbar({ onLogout , onSearch , uploadedFileName }) { // onLogout props 
  const [username, setUser] = useState("-"); // user  
  const [role, setRole] = useState("User"); //    
  const [profileImage, setProfileImage] = useState(() => {
    return sessionStorage.getItem('profileImage') || 'avatar.png';
  });
  const [isDropdownOpen, setIsDropdownOpen] = useState(false); //    
  const [isprofilePopupOpen, setprofileIsPopupOpen] = useState(false); //    
  const [issettingPopupOpen, setIssettingPopupOpen] = useState(false); //    
  const [searchTerm, setSearchTerm] = useState(''); //    
  const [notifications, setNotifications] = useState(0); //   
  const [notificationList, setNotificationList] = useState([]); //    
  const [isNoticeDropdownOpen, setIsNoticeDropdownOpen] = useState(false); //   

  //   
  const handleRoleChange = () => {
    const newRole = window.prompt("   (Admin/User):");
    if (newRole) {
      if (username === " " || " ") { // : IP     
        if (newRole.toLowerCase() === "admin") {
          setRole("Admin");
          alert(`${username} (Admin) .`);
        } else if (newRole.toLowerCase() === "user") {
          setRole("User");
          alert(`${username} (User) .`);
        } else {
          alert("   (Admin/User).");
        }
      } else {
        alert("     .");
      }
    }
  };
  const handleProfileImageChange = (event) => {
    const file = event.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = () => {
        const imageData = reader.result;
        setProfileImage(imageData); //   
        sessionStorage.setItem('profileImage', imageData); //    
      };
      reader.readAsDataURL(file);
    }
  };

  const toggleDropdown = () => {
    setIsDropdownOpen(!isDropdownOpen); //   
  };

  const handleLogout = () => {
    onLogout(); //   
    setIsDropdownOpen(false); //  
  };

  const toggleprofilePopup = () => {
    setprofileIsPopupOpen(!isprofilePopupOpen);
    setIsDropdownOpen(false); //   
  };

  const togglesettingPopup = () => {
    setIssettingPopupOpen(!issettingPopupOpen);
    setIsDropdownOpen(false); //   
  };
  const toggleNoticePopup = () => {
    setIsNoticeDropdownOpen(!isNoticeDropdownOpen);
    setIsDropdownOpen(false); //   
    setNotificationList([]); //   
  };
  const handleSearchChange = (e) => {
    setSearchTerm(e.target.value); //  
  };

  const handleSearchSubmit = (e) => {
    e.preventDefault();
    onSearch(searchTerm); //    
  };
   //   
   const handleNotificationsClick = () => {
    setIsNoticeDropdownOpen(!isNoticeDropdownOpen); //  
    if (notifications > 0) {
      setNotifications(0); //   
    }
  };
  const addNotification = (message) => {
    setNotificationList((prev) => [...prev, message]); //   
    setNotifications((prev) => prev + 1); //   
  };
  
  useEffect(() => {
    if (uploadedFileName) {
      addNotification(`${uploadedFileName} `); //      
    }
  }, [uploadedFileName]); // uploadedFileName   

  return (
    <div className="bg-white flex justify-between items-center p-5 w-full border border-[#737373] space-x-7" >
      <p className='text-3xl font-bold text-[#3a989c]'>
        {username}, .
      </p>
      
       {/*   */}
       <form onSubmit={handleSearchSubmit} className="hidden md:flex" style={{ marginLeft: 'auto' }}>
        <input
          type="text"
          value={searchTerm}
          onChange={handleSearchChange}
          placeholder="..."
          className="border border-gray-300 rounded-l-full p-2 focus:outline-none focus:ring-2 focus:ring-blue-500"
        />
        <button
          type="submit"
          className="border-t border-b border-r border-gray-300 bg-white text-black p-2 rounded-r-full hover:bg-blue-600"
          style={{ borderLeft: 'none' }}
        >
          
        </button>
      </form>

      {/*   */}
      <div className="relative flex flex-col items-end"> {/* flex-col   */}
        <button
          onClick={handleNotificationsClick}
          className="flex items-center justify-center w-12 h-12 bg-[#8f8f8f] rounded-full hover:bg-[#2a7c7c] transition duration-200"
        >
          <span className="text-white text-xl"></span>
          {notifications > 0 && (
            <span className="absolute top-0 right-0 bg-red-500 text-white rounded-full w-5 h-5 flex items-center justify-center text-xs">
              {notifications}
            </span>
          )}
        </button>
  </div>
      {/*   */}
      <div className="relative">
        <button onClick={toggleDropdown}>
          <img
            src={profileImage} //   
            alt="Profile"
            className="w-12 h-12 rounded-full border border-[#737373] cursor-pointer"
          />
        </button>
 

        {/*   */}
        {isDropdownOpen && (
          <div className="absolute right-0 mt-2 w-48 bg-white border border-[#737373] shadow-lg rounded z-10 transform -translate-x-2">
            <ul className="py-2">
              <li 
                className="px-4 py-2 hover:bg-[#28fcf5] hover:text-white cursor-pointer"
                onClick={toggleprofilePopup} //  
              >
                 
              </li>
              <li 
                className="px-4 py-2 hover:bg-[#28fcf5] hover:text-white cursor-pointer"
                onClick={togglesettingPopup} //  
              >
                
              </li>
              <li 
                className="px-4 py-2 hover:bg-[#28fcf5] hover:text-white cursor-pointer"
                onClick={handleLogout} //   
              >
                
              </li>
            </ul>
          </div>
        )}
      </div>

      {/*   */}
      {isprofilePopupOpen && (
        <div className="fixed top-0 right-0 w-1/3 h-full bg-white shadow-lg border border-[#737373] p-5">
          <h2 className="text-lg font-bold"> </h2>
          <img
              src={profileImage}
              alt="  "
              className="w-40 h-40 border border-[#737373]"
            />
          <p>:{username}</p> 
          <p>:{role}</p>
          <button onClick={toggleprofilePopup} className="mt-4 bg-red-500 text-white p-2 rounded"></button>
        </div>
      )}
      {/*   */}
      {issettingPopupOpen && (
        <div className="fixed top-0 right-0 w-1/3 h-full bg-white shadow-lg border border-[#737373] p-5">
          <h2 className="text-lg font-bold"></h2>
          <div className="mb-4">
            <label className="block text-gray-700"> </label>
            <button 
              onClick={() => {
                const newName = window.prompt("  :");
                if (newName) {
                  setUser(newName); //  
                }
              }} 
              className="bg-blue-500 text-white p-2 rounded mt-2"
            >
               
            </button>
          </div>
          <div className="mb-4">
            <label className="block text-gray-700"> </label>
            <input
              type="file"
              accept="image/*"
              onChange={handleProfileImageChange} //    
              className="mt-2"
            />
          </div>
          <div className="mb-4">
            <label className="block text-gray-700"> </label>
            <button
              onClick={handleRoleChange} //    
              className="bg-blue-500 text-white p-2 rounded mt-2"
            >
               
            </button>
            </div>
          
          <button onClick={togglesettingPopup} className="mt-4 bg-red-500 text-white p-2 rounded"></button>
        </div>
          )}
    
    {/*   */}
    {isNoticeDropdownOpen && (
  <div className="absolute right-0 top-20 mt-2 bg-white border border-gray-300 shadow-lg rounded z-10 " style={{ width: '250px' }}>
      <ul className="py-2 max-h-60 overflow-y-auto">
        {notificationList.length === 0 ? (
          <li className="px-4 py-2 text-gray-500"> .</li>
        ) : (
          notificationList.map((notification, index) => (
            <li key={index} className="px-4 py-2 hover:bg-gray-100">
              {notification}
            </li>
          ))
        )}
      </ul>
      {/*       */}
      <button 
        onClick={toggleNoticePopup} 
        className="mt-2 w-full text-red-500 border border-gray-300 p-2 rounded text-center"
      >
        
      </button>
    </div>
  )}
  </div>
  );
}

export default Navbar;