const VideoStream = () => {
    const videoUrl = "https://raw.githubusercontent.com/gf678/test_storage/main/1.mp4"; //  URL
  
    return (
      <div>
        <video width="1280" controls>
          <source src={videoUrl} type="video/mp4" />
              .
        </video>
      </div>
    );
  }
  
  export default VideoStream;