// src/components/Login.js
import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

function Login({ onLogin }) {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [errorMessage, setErrorMessage] = useState('');
  const [isSuccess, setIsSuccess] = useState(false); //    
  const navigate = useNavigate();

  const TEMP_USERNAME = '123'; //  
  const TEMP_PASSWORD = '123';   //  

  //       
  useEffect(() => {
    const storedUsername = sessionStorage.getItem('username');
    if (storedUsername) {
      navigate('/Home'); //       
    } else {
      //  
      setUsername('');
      setPassword('');
      setErrorMessage('');
      setIsSuccess(false);
    }
  }, [navigate]);

  const handleSubmit = (e) => {
    e.preventDefault();

    if (username === TEMP_USERNAME && password === TEMP_PASSWORD) {
      setIsSuccess(true); //     
      sessionStorage.setItem('username', username); //    
      onLogin(); //   

      //      
      setTimeout(() => {
        navigate('/Home'); //   
      }, 2000); //    
    } else {
      setErrorMessage('   .');
    }
  };

  return (
    <div className="min-h-screen flex items-center justify-center bg-cover bg-center" style={{ backgroundImage: 'url(/Login_bg.jpg)' }}>
      <div className={`bg-white p-8 rounded-lg shadow-lg w-96 h-100 transition-all duration-500 ${isSuccess ? 'transform scale-75' : ''}`}>
        {!isSuccess ? (
          <>
            <h2 className="text-2xl font-bold text-center mb-6"></h2>
            {errorMessage && (
              <p className="text-red-500 mb-4 text-center">{errorMessage}</p>
            )}
            <form onSubmit={handleSubmit}>
              <div className="mb-4">
                <label className="block text-gray-700 mb-2" htmlFor="username"></label>
                <input
                  type="text"
                  id="username"
                  value={username}
                  onChange={(e) => setUsername(e.target.value)}
                  className="w-full border border-gray-300 rounded p-2 focus:outline-none focus:ring-2 focus:ring-blue-500"
                  required
                />
              </div>
              <div className="mb-4">
                <label className="block text-gray-700 mb-2" htmlFor="password"></label>
                <input
                  type="password"
                  id="password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  className="w-full border border-gray-300 rounded p-2 focus:outline-none focus:ring-2 focus:ring-blue-500"
                  required
                />
              </div>
              <button
                type="submit"
                className="w-full bg-blue-500 text-white p-2 rounded hover:bg-blue-600"
              >
                
              </button>
            </form>
          </>
        ) : (
          <div className="flex flex-col items-center justify-center">
            <img
              src="/Login_o.png"
              alt="Logo"
              className="w-24 mb-4 transition-transform duration-2000 transform scale-150 mt-20"
            />
            <p className="text-2xl font-bold mt-10">!</p>
          </div>
        )}
      </div>
    </div>
  );
}

export default Login;